.loader {
  border: 16px solid var(--color-gray-2);
  border-top: 16px solid var(--color-blue-2);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
